@media screen and (min-width: 470px) and (max-width: 599px) {
  .cardContainer {
    .card {
      height: 380px;
      margin: 50px 0;
      border-radius: 15px;
      .infos {
        height: 100%;
        padding: 30px;
        border-radius: 15px;
        .details {
          margin-bottom: 10px;
          font-size: globals.$font-size-xs;
        }
      }
    }
  }
}

@media screen and (min-width: 320px) and (max-width: 469px) {
  .cardContainer {
    .card {
      height: 320px;
      margin: 50px 0;
      border-radius: 15px;
      .infos {
        height: 100%;
        padding: 30px;
        border-radius: 15px;
        .details {
          margin-bottom: 10px;
          font-size: globals.$font-size-xxs;
        }
      }
    }
  }
}

@media screen and (max-width: 376px) {
  .cardContainer {
    .card {
      height: 320px;
      margin: 50px 0;
      .infos {
        height: 100%;
        padding: 15px;
        .details {
          font-size: 10px;
        }
      }
    }
  }
}
